@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Line th,
.Line td {
    padding: 10px;
}

.Line .name {
    cursor: pointer;
}

.Line span {
    white-space: nowrap;
}

.ProductsSelection h1{width:100%;text-align:center;color:#fff;font-size:2.5rem}

.BackOrderOption {
    cursor: pointer;
}

.Modal .darken{width:100%;height:100%;background-color:#000;opacity:.5;position:fixed;top:0;left:0;z-index:99}.Modal .content{width:60%;min-height:50%;background-color:#fff;position:fixed;margin:5% 20%;top:0;z-index:999;padding:25px;border:solid 5px #0B3954;border-radius:10px}.Modal .content .close{cursor:pointer;position:absolute;right:25px;text-decoration:underline}

.Order .title-container h1{color:#000;font-size:2.5rem;margin-top:0;padding-top:3vh;border-bottom:solid 5px #0B3954}.Order .table-wrapper{border-radius:10px;overflow:hidden}.Order table{width:100%;background-color:#fff;border-collapse:collapse;caption-side:bottom;color:#331213;border:1px solid #0B3954}.Order table input[type=number]{width:2.5vw;-moz-appearance:textfield;text-align:center}.Order table input[type=number]::-webkit-outer-spin-button,.Order table input[type=number]::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.Order table button{margin-left:1vw}.Order table th,.Order table td{text-align:center;padding:2vh 2vw}.Order table th:last-child,.Order table td:last-child{max-width:7vw;width:7vw}.Order table thead{background-color:#0B3954;color:#fff;border-radius:10px;box-shadow:0 5px 5px -5px #0B3954;font-size:1.15rem}.Order table tbody tr:nth-child(even){background-color:#CED7DC}.Order table tbody td{height:2rem}.Order table caption{background-color:#0B3954;box-shadow:0 -5px 5px -5px #0B3954;padding:0 2vw 2vh;color:#fff;font-size:1.3rem;text-align:right;border-radius:0 0 10px 10px}.Order table caption .psa{text-align:left;width:25%;font-size:1rem;position:absolute;padding-top:2vh}.Order table caption p{margin:2vh 0}.Order .Search{width:100%;background-color:#CED7DC;border-radius:10px;border:solid 5px #0B3954;margin-bottom:2vh;padding:2vh 2vw;box-sizing:border-box}.Order .Search strong{font-size:1.4rem}.Order .Search ul{display:flex;list-style:none;padding:0;justify-content:flex-start}.Order .Search ul li{margin-right:2vw}.Order .Search ul li span{margin-right:.5vw;font-size:1.1rem}.Order .BackOrderModal h1{border-bottom:solid 5px #0B3954}.Order .BackOrderModal span{color:#0B3954;font-size:1.2rem}.Order .BackOrderModal ul{list-style:none;padding:0;display:flex;justify-content:space-between;width:100%}.Order .BackOrderModal ul li{border:solid 5px #0B3954;border-radius:10px;padding:2vh 1vw;flex:1 1;margin-right:1vw}.Order .BackOrderModal ul li strong{font-size:1.3rem;display:block}.Order .BackOrderModal ul li:hover{box-shadow:0 0 5px #082D43;cursor:pointer}.Order .OrderDelivery{display:flex;justify-content:space-between}.Order .OrderDelivery .OrderSummary{border:solid 5px #0B3954;border-radius:10px;padding:0 1vw;width:28vw;background-color:#fff}.Order .OrderDelivery .OrderSummary ul{padding:0;list-style:none}.Order .OrderDelivery .OrderSummary ul li{margin-top:1.5vh;text-align:right;border-bottom:#CED7DC solid 1px;padding-bottom:1.5vh}.Order .OrderDelivery .OrderSummary ul li:last-child{padding:0;border:none}.Order .OrderDelivery .OrderSummary ul li:first-child{padding-top:.5vh}.Order .OrderDelivery .OrderSummary strong:last-child{display:block;width:100%;text-align:right;padding:2vh 0;font-size:1.3rem;border-top:solid 5px #0B3954}.Order .OrderDelivery button{margin-left:24vw;width:6vw;margin-top:2vh}.Order .OrderDelivery .options{display:flex;flex-flow:column;width:35vw}.Order .OrderDelivery .options ul{list-style:none;padding:0;display:flex}.Order .OrderDelivery .options ul li{margin-right:3vw;margin-bottom:5vh;border:solid 5px #0B3954;border-radius:10px;padding:2vh 1vw}.Order .OrderDelivery .options ul li strong{font-size:1.4rem;margin-right:1vw}.Order .OrderDelivery .options h3{font-size:1.4rem}.Order .OrderDelivery .options textarea{width:80%;height:15vh}

.App{padding:0;margin:0;background-image:linear-gradient(#fff, #fff);min-height:110vh;color:#000;font-family:"Alata",sans-serif}.App header{margin-top:0;min-height:10vh;max-width:100vw;background-color:#fdfdff;display:flex;padding:0 3vw}.App #app-content{padding:0 3vw}.App button{border:2px solid #082D43;padding:1vh 1vw;border-radius:5px;background-color:#fff;color:#082D43;font-weight:bold;font-size:1.05rem}.App button:disabled{background-color:#CED7DC;cursor:auto}.App button:disabled:hover{box-shadow:none;cursor:auto}.App button:hover{box-shadow:0 0 5px #082D43;cursor:pointer}.App input,.App select,.App textarea{padding:1vh .5vw;font-family:"Alata",sans-serif;border:1px solid #082D43;border-radius:5px;background-color:#fff}.App input:disabled,.App select:disabled,.App textarea:disabled{background-color:#CED7DC;cursor:auto}.App input:focus,.App select:focus,.App textarea:focus{box-shadow:0 0 5px #082D43}.App .select select{-webkit-appearance:none;-moz-appearance:none;appearance:none;cursor:pointer;border-radius:5px 0 0 5px}.App .select:after{content:'\25bc';font:17px "Consolas", monospace;padding:1vh 5px;color:#fff;pointer-events:none;background-color:#082D43;display:inline-block;border-radius:0 5px 5px 0;border:1px solid #082D43}

